.cardHeader{
  margin: 0 auto;
  text-align: center;
  height: 400px;
  background-size:cover;
  background-position: center;
  max-width: 75%;
  max-height: 50%;
  overflow: hidden;
  position: relative;
}


.headerBackground{
  opacity: 0.6;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: auto;
  object-fit: fill;
}

.headerText{
  font-size: 100px;
  position: relative;
}

